'use client';
import React, { useState, useRef } from "react"; 
import { Navbar, Nav, Container, Row, Col, Dropdown, NavDropdown, Button } from "react-bootstrap";
import Logo from '../../public/Images/logo.jpg';
// import ContactPopup from "../Contactpopup";
import './style.css';
import { FaFacebookF, FaTwitter, FaInstagram, FaLinkedinIn, FaComments } from 'react-icons/fa';

function Header() {
  const [show, setShow] = useState(false);
  const navRef = useRef(null);

  const handleNavClick = () => {
    if (navRef.current) {
      navRef.current.collapse();
    }
  };

  return (
    <>
      <div className='top-header' style={{ backgroundColor: '#5458f7', color: 'white', padding: '8px 0', fontSize: '15px' }}>
        <Container>
          <Row className='align-items-center'>
            <Col className='d-flex align-items-center'>
              <span className='me-3'>ðŸ“§ info@yourcompany.com</span>
              <span className='me-3'>ðŸ“ž +92 300 000 0000</span>
              <span className='d-flex align-items-center'><FaComments /> Chat</span>
            </Col>
            <Col className='d-flex justify-content-end gap-3'>
              <a href='#' style={{ color: 'white' }}><FaFacebookF /></a>
              <a href='#' style={{ color: 'white' }}><FaTwitter /></a>
              <a href='#' style={{ color: 'white' }}><FaInstagram /></a>
              <a href='#' style={{ color: 'white' }}><FaLinkedinIn /></a>
            </Col>
          </Row>
        </Container>
      </div>

      <Navbar expand="lg" className="py-3 shadow-sm bg-white" ref={navRef}>
        <Container fluid>
          <Navbar.Brand href="/">
            <img
              src={Logo}
              alt="Logo"
              width="100"
              className="img-fluid"
            />
          </Navbar.Brand>

          <Navbar.Toggle aria-controls="main-navbar" />

          <Navbar.Collapse id="main-navbar">
            <Nav className="mx-auto">
              <Nav.Link href="/" onClick={handleNavClick}>Home</Nav.Link>

              <NavDropdown title="Services" id="mega-menu" onClick={handleNavClick}>
                <div className="p-4" style={{ minWidth: "800px" }}>
                  <Row>
                    <Col md={6}>
                      <h6 className="fw-bold">Textile</h6>
                      <NavDropdown.Item href="/tennis-clothing-manufacturer">Tennis Clothing Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/golf-apparel-manufacturer">Golf Apparel Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/sweat-suit-manufacturer">Sweat Suit Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/gym-clothing-manufacturer">Gym Clothing Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Cycling-clothing-manufacturers">Cycling Clothing Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Gymnastics-leotard-manufacturers">Gymnastics Leotard Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-jackets-manufacturer">Custom Jackets Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-shorts-manufacturer">Custom Shorts Manufacturer</NavDropdown.Item>
                    </Col>
                    <Col md={6}>
                      <h6 className="fw-bold">Apparel</h6>
                      <NavDropdown.Item href="/Yoga-wear-manufacturer">Yoga Wear Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Hunting-clothing-manufacturer">Hunting Clothing Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-tracksuit-manufacturers">Custom Tracksuit Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Baseball-clothing-manufacturers">Baseball Clothing Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-hoodie-manufacturer">Custom Hoodie Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-underwear-manufacturer">Custom Underwear Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-pajama-manufacturers">Custom Pajama Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Fishing-shirt-manufacturers">Fishing Shirts Manufacturing</NavDropdown.Item>
                    </Col>
                  </Row>
                </div>
              </NavDropdown>

              <Nav.Link href="/portfolio" onClick={handleNavClick}>Portfolio</Nav.Link>
              <Nav.Link href="/about-us" onClick={handleNavClick}>About</Nav.Link>
              <Nav.Link href="/services" onClick={handleNavClick}>Blogs</Nav.Link>
              <Nav.Link href="/contact-us" onClick={handleNavClick}>Contact</Nav.Link>
            </Nav>

            <div className="ms-auto">
              <Button variant="outline-primary" onClick={() => setShow(true)}>
                GET STARTED
              </Button>
              <ContactPopup show={show} handleClose={() => setShow(false)} />
            </div>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
}

export default Header;
